@media only screen and (max-width: 600px) {
  /* make all the boxes appear in a single column */
  .MuiBox-root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }
   /* adjust the height and width of the cards  */
  .MuiCard-root {
  height: 160px;
  width: 160px;
  }
  /* adjust the font sizes */
  .MuiTypography-h4 {
  font-size: 1.8rem;
  }
  .MuiTypography-h5 {
  font-size: 1.2rem;
  }
  .MuiTypography-h6 {
  font-size: 1rem;
  }
  /* adjust the icon sizes */
  .MuiSvgIcon-root {
  font-size: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .MuiCard-root {
  flex-basis: 100%;
  }
  }